import React from 'react'
import PropTypes from 'prop-types'
import '../styling/Style.css'


export default function Navbar(props) {



    return (
        <>
            <nav className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}>
                <div className="container-fluid">
                    <a className="navbar-brand" >{props.title}</a>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                           
                        </ul>
                        <form className="d-flex">
                            <div className="form-check form-switch">
                                <input onClick={props.enableMode} className="form-check-input" type="checkbox" id="flexSwitchCheckDefault" />
                                <label style={props.Style} className="form-check-label mx-2" htmlFor="flexSwitchCheckDefault">{props.modeText}</label>
                            </div>
                        </form>

                    </div>
                </div>
            </nav>
        </>
    )
}
Navbar.propTypes = {
    title: PropTypes.string.isRequired,
    about: PropTypes.string
}
